.cello-fanpage {
	.swiper-container {
		@include pad(10px null);
		.item {
			@include bor-rad(5px);
			overflow: hidden;
			@include tran(.4s);
			text-decoration: none;
			&:hover {
				box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
				.buy-now {
					opacity: 1; } }
			.top-item {
				position: relative;
				.img {
					text-align: center;
					img {
						width: 100%; } }
				.icon {
					@include pos(abs, null null 0 0);
					width: 50px;
					height: 40px;
					background: rgba(41, 72, 125, .8);
					text-align: center;
					i {
						color: #ffffff;
						line-height: 40px;
						@include fz(1.4rem); } } }
			.bottom-item {
				background: #f5f5f5;
				padding-bottom: r(28px);
				@include pad(null r(12px));
				padding-top: r(12px);
				.time {
					display: flex;
					p {
						+ p {
							margin-left: .5rem; }
						@include fz(13px);
						@include lh(20px);
						@include fw(400);
						color: #999999; } }
				.name {
					padding-top: r(12px);
					h5 {
						@include fz(r(14px));
						@include fw(400);
						@include lh(20px);
						color: #414042;
						margin-bottom: 0;
						text-transform: uppercase;
						span {
							@include fw(600);
							color: #ff6600; } } } } } } }
