.cello-newsSlide {
	@include pad(10px null);
	.swiper-container {
		.swiper-wrapper {
			.swiper-slide {
				padding-left: 20px;
				position: relative;
				a {
					margin-bottom: 0;
					@include fz(r(14px));
					@include fw(300);
					display: -webkit-box;
					-webkit-box-orient: vertical;
					-webkit-line-clamp: 2;
					overflow: hidden;
					color: #fff; }
				&:after {
					content: '';
					width: 1px;
					height: 100%;
					background: #666666;
					@include pos(abs, 0 null null 0); }
				&:before {
					content: '';
					width: 0px;
					height: 0px;
					border-top: 7px solid transparent;
					border-bottom: 7px solid transparent;
					border-left: 7px solid #666666;
					@include pos(abs, 50% null null 0);
					transform: translateY(-50%); } } } } }
