.cello-baohanh {
	@include pad(r(30px) null);
	@include media-breakpoint-up(lg) {
		@include pad(r(50px) null); }
	.cello-title {
		text-align: left;
		@media (max-width: 575.9px) {
			text-align: center; }
		h2 {
			font-size: 36px;
			text-transform: uppercase;
			color: #333; }
		p {
			font-size: 15px;
			font-weight: 400;
			color: #737d83; } }
	.search-main {
		display: flex;
		.custom-select {
			flex: 0 0 180px;
			max-width: 180px;
			background: black;
			border-top-left-radius: 25px;
			border-bottom-left-radius: 25px;
			select {
				text-transform: uppercase;
				background: transparent;
				border: 0;
				width: 100%;
				line-height: 50px;
				height: 50px;
				padding: 0 20px;
				font-size: 15px;
				font-weight: 500;
				color: white;
				&:focus {
					outline: none; }
				option {
					color: black; } } }
		.text-input {
			position: relative;
			width: 100%;
			input {
				line-height: 50px;
				height: 50px;
				padding: 0 20px;
				width: 100%;
				border: 0;
				border-top-right-radius: 25px;
				border-bottom-right-radius: 25px;
				box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1);
				font-size: 13px;
				font-weight: 300;
				color: #333;
				&::placeholder {
					font-size: 13px;
					font-weight: 300;
					color: #737d83; }
				&:focus {
					outline: none; } }
			a {
				position: absolute;
				top: 2px;
				right: 2px;
				width: 46px;
				height: 46px;
				border-radius: 50%;
				border: 0;
				color: white;
				display: flex;
				justify-content: center;
				align-items: center;
				background-image: linear-gradient(90deg, rgb(46,177,53) 0%, rgb(0,152,74) 100%, rgb(78,147,12) 100%, rgb(82,194,23) 100%, rgb(117,165,80) 100%, rgb(110,175,64) 100%, rgb(122,193,29) 100%, rgb(180,217,133) 100%, rgb(168,212,113) 100%, rgb(162,210,97) 100%, rgb(176,214,102) 100%); } } }
	.search-results {
		box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1);
		margin-top: 50px;
		background: white;
		padding: 30px 20px;
		border-radius: 5px;
		h4 {
			font-size: 25px;
			font-weight: 700;
			color: #333;
			text-transform: uppercase;
			margin-bottom: 10px; }
		.result-wrapper {
			border-radius: 5px;
			border: 1px solid #ebebeb;
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 0; } }
		.table {
			padding: 15px 0;
			text-align: left;
			border-bottom: 1px dashed #ebebeb;
			.tr {
				display: flex;
				flex-wrap: wrap;
				+ .tr {
					margin-top: 15px; } }
			.fieldname {
				flex: 0 0 150px;
				max-width: 150px;
				padding: 0 15px;
				font-size: 14px;
				font-weight: 400;
				color: #999999; }
			.value {
				flex-grow: 1;
				flex-basis: 0;
				font-size: 14px;
				font-weight: 400;
				padding: 0 20px;
				color: #333333; } } }
	.note-wrapper {
		width: 100%;
		max-width: 400px;
		margin-left: auto;
		border: 1px solid rgb(215, 215, 215);
		background-color: rgb(239, 239, 239);
		padding: 28px;
		font-size: 15px;
		font-weight: 400;
		color: #737d83;
		@media (max-width: 991.9px) {
			margin: 0 auto;
			max-width: 100%;
			margin-top: 30px; }
		p {
			a {
				color: #ff6600; } } } }
