.cello-spList, .cello-thuonghieu-ct {
	background: white;
	// background: #f7f7f7
	@include pad(r(30px) null);
	@include media-breakpoint-up(lg) {
		padding-bottom: r(50px); }
	border-bottom: 1px solid #f7f7f7;
	.filter-title {
		display: none; }
	.filter-nav {
		display: flex;
		select {
			min-width: 180px;
			background: white;
			border-radius: 50px;
			padding: .3rem 1rem;
			transition: all .2s ease-in-out;
			box-shadow: 0 0 5px 0 rgba(0,0,0,.1);
			position: relative;
			border: 0;
			background-image: url('../img/custom-select.png');
			background-repeat: no-repeat;
			background-position: top right;
			appearance: none;
			&:focus {
				outline: none; } }
		.filter {
			select {
				margin-right: 10px;
				margin-bottom: 10px; } }
		.sort {
			margin-left: auto; } }
	@media (max-width: 991.9px) {
		.filter-title {
			display: block;
			h2 {
				display: flex;
				justify-content: space-between;
				font-size: 18px;
				font-weight: 500;
				color: map-get($mau, main); } }
		.filter-nav {
			flex-direction: column;
			display: none;
			&.active {
				display: flex; }
			select {
				width: 100%; }
			.sort {
				margin-left: 0; }
			.filter {
				select {
					width: 100%;
					+ select {
						margin-left: 0;
						margin-top: 10px; } } }
			.ajaxfilterresponse {
				margin-bottom: 10px;
				display: flex;
				flex-direction: column; } } }
	@include media-breakpoint-up(md) {
		.cello-tabContent {
			margin-top: r(40px);
