.cello-thuonghieu-ct {
	.cello-select {
		display: flex;
		.select {
			+ .select {
				margin-left: 15px; }
			width: 200px;
			height: 35px;
			background: #ffffff;
			line-height: 35px;
			border-radius: 35px;
			.label {
				font-size: 15px;
				display: flex;
				justify-content: space-between;
				align-items: center;
				padding: 0 20px; } } }
	.cello-navTab {
		.cello-tabList {
			justify-content: flex-start;
			text-align: center;
			.item {
				+ .item {
					border-top: 1px solid #2eb135; }
				a {
					border-radius: 0;
					box-shadow: none;
					background: none;
					@include pad(.5rem 0);
					box-shadow: none;
					&.active, &:hover {
						box-shadow: none;
						background: none;
						color: #2eb135; } } } } }
	@include media-breakpoint-up(lg) {
		.cello-navTab {
			.cello-tabList {
				.item {
					+ .item {
						border-top: none; }
					a {
						background: #fff;
						@include bor-rad(50px);
						box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1); } } } }
		.cello-tabContent {
			.list-item {
				.item {
					&:hover {
						background: #fff; } } } } } }
