.cello-ttNav {
	@include pad(10px null);
	background: #f7f7f7;
	.row {
		@include mar(null -5px);
		.item {
			margin-top: r(10px);
			@include pad(null 5px);
			figure {
				margin-bottom: 0;
				@include bor-rad(5px);
				overflow: hidden;
				position: relative;
				.img {
					img {
						width: 100%; } }
				&:after {
					content: '';
					width: 100%;
					@include tran(.4s);
					height: 100%;
					background: rgba(0,0,0,.5);
					@include pos(abs, 0 null null 0); }
				figcaption {
					@include pos(abs, 0 null null 0);
					width: 100%;
					z-index: 10;
					height: 100%;
					@include flex-center();
					h5 {
						text-align: center;
						@include fz(r(16px));
						color: #fff;
						@include fw(500); } } }
			&:hover {
				figure {
					&:after {
						opacity: 0; } } } } }
	@include media-breakpoint-up(lg) {
		.row {
			.item {
				+ .item {
					margin-top: 0; } } } } }
