.cello-lienhe-1 {
	@include pad(r(30px) null);
	@include media-breakpoint-up(lg) {
		@include pad(r(50px) null); }
	.cello-title {
		margin-bottom: 2rem; }
	.list-item {
		.item {
			+ .item {
				margin-top: 2rem;
				@include media-breakpoint-up(lg) {
					margin-top: 0; } }
			figure {
				text-align: center;
				margin-bottom: 0;
				figcaption {
					padding-top: r(25px);
					h5 {
						@include fz(r(15px));
						text-transform: uppercase;
						color: #737d83; }
					p {
						@include fz(r(15px));
						color: #414042;
						margin-bottom: r(10px);
						a {
							color: #ff6600; } } } } } } }
