.cello-spNav {
	background: #f7f7f7;
	.swiper-container {
		.swiper-slide {
			a {
				text-decoration: none;
				border: 1px solid #e1e1e1;
				@include pad(1rem);
				@include flex-center();
				img {
					height: 32px; }
				span {
					@include fz(r(14px));
					text-transform: uppercase;
					color: #999999;
					@include lh(32px); } } } } }
