.cello-thuonghieu {
	padding: 60px 0;
	@media (max-width: 991.98px) {
		padding: 40px 0; }
	.cello-tabContent {
		.list-item {
			overflow: hidden;
			padding: 15px;
			margin: -15px;
			&.show {
				height: unset!important; }
			.item {
				margin-bottom: 20px;
				.top-item {
					.img {
						padding: 20px 10px; } } } } }
	@include media-breakpoint-up(lg) {
		.cello-navTab {
			.cello-tabList {
				.item {
					background: #fff;
					@include bor-rad(50px);
					box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1); } } }
		.cello-tabContent {
			.list-item {
				.item {
					&:hover {
						box-shadow: 0 0 0 0;
						.top-item {
							border-radius: 4px;
							background: #fff;
							box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2); } } } } } }
	@include media-breakpoint-up(xl) {
		.cello-tabContent {
			.list-item {} } } }
