.checkout-step-wrap {
	.row {
		margin: 0;
		overflow: hidden; }
	.step {
		background: darken(white,6%);
		padding: 14px 0;
		flex: 0 0 25%;
		@media (min-width: 576px) {
			padding-left: 20px; }
		@media (min-width: 768px) {
			position: relative;
			&:after,&:before {
				position: absolute;
				width: 0;
				height: 0;
				top: 50%;
				left: 100%;
				z-index: 1;
				border-style: solid;
				content: ''; }
			&:after {
				border-width: 26px 0 26px 26px;
				transform: translateY(-50%);
				border-color: transparent transparent transparent darken(white,6%); }
			&:before {
				border-width: 27px 0 27px 27px;
				transform: translateY(-50%) translateX(2px);
				border-color: transparent transparent transparent darken(white,12%); } }

		@media (min-width: 992px) {
			padding-left: 50px; }
		p {
			margin-bottom: 0;
			@media (min-width: 576px) {
				display: flex; }
			span {
				margin-top: 0;
				display: flex;
				font-weight: 800;
				text-transform: uppercase;
				justify-content: center;
				font-size: 11px;
				@media (min-width: 576px) {
					display: inline-flex;
					font-size: r(13px); }
				@media (min-width: 992px) {
					font-size: r(15px); }
				&:first-child {
					font-size: 0;
					&:before {
						font-family: 'Font Awesome 5 Free';
						font-weight: 900;
						font-size: r(15px);
						-moz-osx-font-smoothing: grayscale;
						-webkit-font-smoothing: antialiased;
						display: inline-block;
						font-style: normal;
						font-variant: normal;
						text-rendering: auto; }
					@media (min-width: 576px) {
						padding-right: 1rem; } }
				&:last-child {
					color: lighten(black,25%);
					font-weight: 500;
					@media (min-width: 576px) {
						justify-content: flex-start; } } } }
		&:nth-child(1) {
			p {
				span:first-child {
					font-size: 0;
					&:before {
						content: '\f07a'; } } } }
		&:nth-child(2) {
			p {
				span:first-child {
					font-size: 0;
					&:before {
						content: '\f007'; } } } }
		&:nth-child(3) {
			p {
				span:first-child {
					font-size: 0;
					&:before {
						content: '\f044'; } } } }
		&:nth-child(4) {
			p {
				span:first-child {
					font-size: 0;
					&:before {
						content: '\f09d'; } } } }
		&.finished {
			background: #737d83;
			&:after {
				border-left-color: #737d83; }
			span {
				&:first-child {
					color: white;
					&:before {
						content: '\f00c'; } }
				&:last-child {
					color: white; } } }
		&.active {
			p {
				span {
					&:nth-child(2) {
						color: #ff6600; }
					&:nth-child(1) {
						color: #737d83; } } } } } }
