.canhcam-footer {
	background-color: #f5f5f5;
	position: relative;
	overflow: hidden;
	color: #333333;
	.footer-content {
		@include pad(1rem null);
		.footer-item {
			+ .footer-item {
				margin-top: .7rem; }
			.footer-title {
				h4 {
					@include fz(15px);
					margin-bottom: 1rem;
					text-transform: uppercase;
					@media (max-width: 991.98px) {
						margin-bottom: .7rem; } } }
			.footer-list {
				.footer-link {
					margin-bottom: 0;
					display: flex;
					align-items: flex-start;
					em {
						line-height: 19pxmargin;
						&:before {
							line-height: 19pxmargin; } }
					&:first-child {
						margin-bottom: 1rem; }
					p {
						margin-left: 6px;
						@include fz(13px);
						span {
							color: #ff6600; } } } }
			.note {
				p {
					@include fz(r(13px));
					color: #666666; } }
			.img {
				margin-top: 1rem;
				img {
					max-width: 100%; } }
			.list-group {
				.list-group-item {
					a {
						@include pad(r(6px) null);
						color: #666666;
						text-decoration: none;
						color: {}
						@include fz(13px);
						display: block;
						@include tran(.4s);
						&:hover {
							color: #2eb135; } } } }
			.form {
				@include mar(1rem null);
				display: flex;
				position: relative;
				input {
					width: 100%;
					border-radius: 50px;
					min-height: 40px;
					padding-right: 50px;
					border: none;
					font-size: .8rem;
					padding-left: .8rem;
					&:focus {
						box-shadow: none;
						outline: none; } }
				button {
					@include pos(abs, 0 0 null null);
					display: block;
					width: 40px;
					height: 40px;
					background: #000000;
					border-radius: 50px;
					text-align: center;
					font-size: 0;
					border: 0;

					&:before {
						content: '\f054';
						font: normal normal normal 24px/1 "Material Design Icons";
						line-height: 40px;
						color: #ffffff;
						font-size: 1rem; }
					&:focus {
						outline: 0; } } }
			.social {
				.list-inline {
					display: flex;
					margin-bottom: 0;
					padding-left: 0;
					justify-content: center;
					.list-inline-item {
						+ .list-inline-item {
							margin-left: 1rem; }
						min-width: 32px;
						min-height: 32px;
						text-align: center;
						background: #ffffff;
						border-radius: 50%;
						list-style: none;
						a {
							text-decoration: none;
							line-height: 32px;
							color: #737d83; } } } } }
		.mobile-dropdown {
			@media (max-width: 575.9px) {
				.footer-title {
					position: relative;
					&:before {
						content: '\f419';
						font-family: 'Material Design Icons';
						position: absolute;
						right: 0;
						font-size: 20px;
						top: 50%;
						transform: translateY(-50%); }
					&.mobile-active {
						&:before {
							content: '\f377'; } } }

				.list-group {
					display: none; } } } }
	.nav-bottom {
		@include pad(1rem null);
		background-color: #000;
		@include fz(.95rem);
		.copyright {
			text-align: center;
			p {
				line-height: 22px;
				@include fz(12px);
				color: #999999;
				a {
					line-height: inherit;
					font-size: inherit;
					color: inherit;
					text-decoration: none; } } }
		.nav {
			margin-bottom: 0;
			padding-left: 0;
			display: flex;
			justify-content: center;
			flex-flow: wrap;
			.nav-item {
				list-style: none;
				+ .nav-item {
					padding-left: .35rem;
					margin-left: .35rem;
					position: relative;
					&:before {
						content: '';
						width: 1px;
						height: 12px;
						background: #999999;
						@include pos(abs, 50% null null 0);
						transform: translateY(-50%); } }
				.nav-link {
					@include fz(12px);
					color: #999999; } } } }
	@include media-breakpoint-up(lg) {
		.footer-content {
			@include pad(2rem null);
			.footer-item {
				+ .footer-item {
					margin-top: 0; }
				&:last-child {
					margin-top: 2rem; } } }
		.nav-bottom {
			.copyright {
				text-align: left; }
			.nav {
				justify-content: flex-end; } } }
	@include media-breakpoint-up(xl) {
		.footer-content {
			.footer-item {
				+ .footer-item {
					margin: 0; } } } } }
