		// 			transform: translateY(-50%) rotate(180deg)

		.cello-mbTab {
			display: none; }
		.cello-tabList {
			display: flex;
			background: none;
			overflow-x: auto;
			&::-webkit-scrollbar {
				background: rgba(0,0,0,.2);
				height: 5px; }
			&::-webkit-scrollbar-thumb {
				background: rgb(122,193,29); }
			.item {
				@include mar(null 5px);
				color: #999999;
				cursor: pointer;
				user-select: none;
				@include fz(r(16px));
				@include tran(.4s);
				transition: .5s all ease-in-out;
				@media (max-width: 991.98px) {
					flex: 0 0 33.3333%;
					max-width: 33.3333%;
					margin: 0;
					margin-bottom: 10px;
					padding: 0 5px;
					p {
						margin-top: 10px;
						font-size: 14px;
						text-align: center; } }
				+ .item {
					border: none; }
				.img {
					margin: auto;
					width: 60px;
					height: 60px;
					line-height: 60px;
					transition: .5s all ease-in-out;
					border-radius: 50px;
					text-align: center;
					background: rgb(255, 255, 255);
					box-shadow: 0px 3px 8px 0px rgba(0, 0, 0, 0.2); }
				&:hover, &.active {
					.img {
						background-image: -moz-linear-gradient( 0deg, rgb(46,177,53) 0%, rgb(0,152,74) 100%, rgb(78,147,12) 100%, rgb(82,194,23) 100%, rgb(117,165,80) 100%, rgb(110,175,64) 100%, rgb(122,193,29) 100%, rgb(180,217,133) 100%, rgb(168,212,113) 100%, rgb(162,210,97) 100%, rgb(176,214,102) 100%);
						background-image: -webkit-linear-gradient( 0deg, rgb(46,177,53) 0%, rgb(0,152,74) 100%, rgb(78,147,12) 100%, rgb(82,194,23) 100%, rgb(117,165,80) 100%, rgb(110,175,64) 100%, rgb(122,193,29) 100%, rgb(180,217,133) 100%, rgb(168,212,113) 100%, rgb(162,210,97) 100%, rgb(176,214,102) 100%);
						background-image: -ms-linear-gradient( 0deg, rgb(46,177,53) 0%, rgb(0,152,74) 100%, rgb(78,147,12) 100%, rgb(82,194,23) 100%, rgb(117,165,80) 100%, rgb(110,175,64) 100%, rgb(122,193,29) 100%, rgb(180,217,133) 100%, rgb(168,212,113) 100%, rgb(162,210,97) 100%, rgb(176,214,102) 100%);
						img {
							filter: brightness(0) invert(1); } } } } } }

	@include media-breakpoint-up(lg) {
		.cello-navTab {
			.cello-mbTab {
				display: none; }
			.cello-tabList {
				justify-content: flex-start;
				display: flex;
				background: none;
				.item {
					+ .item {
						border: none;
						margin-left: 40px; }
					.img {
						margin: auto;
						width: 90px;
						height: 90px;
						transition: .5s all ease-in-out;
						border-radius: 50px;
						line-height: 90px;
						text-align: center;
						background: rgb(255, 255, 255);
						box-shadow: 0px 3px 8px 0px rgba(0, 0, 0, 0.2);
						margin-bottom: 15px; }
					@include mar(null 5px);
					color: #999999;
					cursor: pointer;
					user-select: none;
					@include fz(r(16px));
					@include tran(.4s);
					transition: .5s all ease-in-out;
					&:hover, &.active {
						.img {
							background-image: -moz-linear-gradient( 0deg, rgb(46,177,53) 0%, rgb(0,152,74) 100%, rgb(78,147,12) 100%, rgb(82,194,23) 100%, rgb(117,165,80) 100%, rgb(110,175,64) 100%, rgb(122,193,29) 100%, rgb(180,217,133) 100%, rgb(168,212,113) 100%, rgb(162,210,97) 100%, rgb(176,214,102) 100%);
							background-image: -webkit-linear-gradient( 0deg, rgb(46,177,53) 0%, rgb(0,152,74) 100%, rgb(78,147,12) 100%, rgb(82,194,23) 100%, rgb(117,165,80) 100%, rgb(110,175,64) 100%, rgb(122,193,29) 100%, rgb(180,217,133) 100%, rgb(168,212,113) 100%, rgb(162,210,97) 100%, rgb(176,214,102) 100%);
							background-image: -ms-linear-gradient( 0deg, rgb(46,177,53) 0%, rgb(0,152,74) 100%, rgb(78,147,12) 100%, rgb(82,194,23) 100%, rgb(117,165,80) 100%, rgb(110,175,64) 100%, rgb(122,193,29) 100%, rgb(180,217,133) 100%, rgb(168,212,113) 100%, rgb(162,210,97) 100%, rgb(176,214,102) 100%);
							img {
								filter: brightness(0) invert(1); } } } } } } } }
