.cello-breadcrumb {
	background: #efefef;
	position: relative;
	.main-breadcrumb {
		width: 100%;
		.breadcrumb {
			display: flex;
			padding: 0;
			list-style: none;
			margin-bottom: 0;
			align-items: center;
			background: transparent;
			li {
				display: flex;
				align-items: center;
				@include fz(r(13px));
				a {
					color: #999999; }
				+ li {
					&:before {
						@include lnr('\e876');
						@include fz(r(42px));
						@include pad(null .5rem);
						color: #e0e0e0; } }
				&:last-child {
					a {
						color: #404040; } } } } } }
