.user-sidemenu {
	padding: 50px 10px 80px 10px; }
.user-sidebar {
	figure {
		display: flex;
		align-items: center;
		img {
			height: 70px;
			flex: 0 0 70px;
			border-radius: 50%; }
		figcaption {
			flex-grow: 1;
			flex-basis: 0;
			max-width: none;
			display: flex;
			flex-wrap: wrap;
			padding-left: 15px;
			height: 100%;
			.title {
				font-size: 14px;
				font-weight: 400;
				color: darken(white,36%);
				width: 100%;
				margin-bottom: 0; }
			.user-name {
				font-size: 18px;
				font-weight: 500;
				color: lighten(black,9%);
				width: 100%;
				margin-bottom: 0;
				text-transform: uppercase; } } } }


.user-sidelink {
	@media (max-width: 991.9px) {
		margin-bottom: 30px; }
	ul {
		li {
			margin-bottom: 5px;
			a {
				font-size: 15px;
				font-weight: 400;
				color: darken(white,40%);
				text-transform: uppercase;
				display: flex;
				align-items: center;
				height: 40px;
				border-radius: 20px;
				padding: 0 24px;
				border: 1px solid transparent;
				text-decoration: none; }
			&:hover {
				a {
					border: 1px solid #ff6600; } }
			&.active {
				a {
					background: #ff6600;
					color: white; } } } } }

.user-page {
	position: relative;
	.address-info,.account-info {
		.heading {
			margin-bottom: 8px; } }
	.postname {
		font-size: 25px;
		font-weight: 700;
		color: lighten(black,20%);
		text-transform: uppercase;
		margin-bottom: 24px; }
	>.row {
		@media (max-width: 991.9px) {
			margin-bottom: 30px; }
		.col-xs-12 {
			flex: 0 0 100%;
			max-width: 100%;
			padding: 0 10px; } }
	.heading {
		// margin-bottom: 8px
		.link {
			display: inline-block;
			font-size: 0;
			&:before {
				content: "Chỉnh sửa";
				border-left: 1px solid #a3a3a3;
				padding-left: 5px;
				margin-left: 5px;
				font-size: 13px;
				font-weight: 400;
				color: #a3a3a3; }
			&:hover {
				color: map-get($mau, main); } }
		.posttitle {
			display: inline-block;
			font-size: 16px;
			font-weight: 600;
			color: #333333; } }
	.info-wrapper {
		padding: 20px;
		padding-bottom: 32px;
		background-color: rgb(255, 255, 255);
		box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1); }
	.info-detail {
		.detail {
			font-size: 15px;
			font-weight: 400;
			color: lighten(black,20%); } }
	.account-info {
		box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1);
		background: white;
		height: 100%; }
	.cart-history.info-wrapper {
		padding: 0;
		margin-top: 30px;
		.heading {
			padding: 20px; }
		.table-responsive {
			margin: 0;
			display: block;
			overflow-x: auto; }
		table {
			width: 100%;
			font-size: 15px;
			min-width: 700px;
			th,td {
				label {
					display: inline; }
				&:first-child {
					padding-left: 20px; } }
			thead {
				background: #f7f7f7;
				th {
					color: #666666;
					font-weight: 600;
					height: 50px;
					vertical-align: middle;
					@media (max-width: 767.9px) {
						padding: 0 10px; }
					&:nth-child(1), &:nth-child(3), &:nth-child(4), &:nth-child(5) {
						width: 15%; } } }
			tbody {
				color: #333333;
				tr {
					border-top: 1px solid darken(white,10%);
					&:nth-child(2n) {
						background: #f7f7f7; } }
				td {
					font-weight: 400;
					height: 60px;
					vertical-align: middle;
					@media (max-width: 767.9px) {
						padding: 0 10px; }
					a {
						color: #333333;
						font-weight: 400; }
					&.thing_history {
						color: #0ba147; } } } } }

	.address-list {
		margin: 0 -20px;
		display: flex;
		flex-wrap: wrap;
		>.col-md-6 {
			padding: 0 20px;
			&:nth-child(2n) {
				border-left: 1px solid darken(white,20%); }
			@media (max-width: 767.9px) {
				border-left: 0;
				border-bottom: 1px solid rgba(0,0,0,.2);
				padding-top: 15px;
				padding-bottom: 15px;
				&:nth-child(2n) {
					border-left: 0; } } }
		.address {
			background: white;
			height: 100%;
			position: relative;
			.type {
				margin-top: 15px;
				color: #ff6600;
				font-size: 14px;
				&:before {
					font-size: 12px;
					content: '\f12c';
					font-family: 'Material Design Icons';
					border-radius: 50%;
					border: 1px solid #f60;
					width: 16px;
					height: 16px;
					color: #f60;
					display: inline-flex;
					justify-content: center;
					align-items: center;
					margin-right: 5px; } }
			.name {
				font-size: 15px;
				font-weight: 500;
				color: #414042;
				text-transform: uppercase;
				margin-bottom: 8px; }
			ul,li {
				font-size: 13px;
				font-weight: 400;
				color: darken(white,40%); }

			.btn-editaddr, .btn-deleteaddr {
				text-decoration: none;
				margin-top: 24px;
				display: inline-flex;
				justify-content: center;
				align-items: center;
				width: 160px;
				height: 40px;
				border: 1px solid #11a34b;
				border-radius: 20px;
				font-size: 14px;
				font-weight: 600;
				color: #999999;
				text-transform: uppercase; }
			.btn-deleteaddr {
				margin-left: 20px;
				@media (max-width: 575.9px) {
					margin-left: 0; } } } }
	.account-form {
		@media (max-width: 991.9px) {
			display: flex;
			flex-direction: column; }
		.form-group {
			display: flex;
			align-items: center;
			margin-bottom: 20px;
			&.settingrow {
				display: none; }
			@media (max-width: 991.9px) {
				flex-wrap: wrap; }
			label {
				flex: 0 0 230px;
				max-width: 230px;
				font-size: 16px;
				font-weight: 500;
				color: lighten(black,20%); }

			span.RadInput_Default {
				width: 100%; }
			input[type="text"], input[type="password"], select {
				width: 100%;
				height: 45px;
				border-radius: 22.5px;
				background: darken(white,4%);
				border: 0;
				padding: 0 20px;
				&:focus {
					outline: none; } }
			span.RadInput_Default, input[type="text"], input[type="password"], select, .settingcontrol, >div {
				flex: 0 0 500px;
				max-width: 500px;
				@media (max-width: 991.9px) {
					flex: 0 0 100%;
					max-width: 100%; } } } }
	.button-wrap {
		>div {
			display: flex; } }
	input[type="submit"] {
		margin-left: 230px;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		height: 50px;
		border-radius: 25px;
		min-width: 195px;
		padding: 0 2rem;
		cursor: pointer;
		text-decoration: none;
		text-transform: uppercase;
		background-image: linear-gradient( 90deg, rgb(46,177,53) 0%, rgb(0,152,74) 100%, rgb(78,147,12) 100%, rgb(82,194,23) 100%, rgb(117,165,80) 100%, rgb(110,175,64) 100%, rgb(122,193,29) 100%, rgb(180,217,133) 100%, rgb(168,212,113) 100%, rgb(162,210,97) 100%, rgb(176,214,102) 100%);
		color: white;
		border: 0;
		span {
			color: white; }
		@media (max-width: 991.9px) {
			padding: 0 16px;
			min-width: 195px;
			margin: auto; } }
	.btn-addaddr {
		position: absolute;
		right: 0;
		top: 0;
		margin-left: auto;
		width: 160px;
		height: 40px;
		border: 1px solid #11a34b;
		border-radius: 20px;
		font-size: 14px;
		font-weight: 600;
		color: darken(white,40%);
		text-transform: uppercase;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 10px;
		text-decoration: none;
		&:after {
			margin-left: 8px;
			content: '\f303';
			font-weight: 900;
			font-family: 'Font Awesome 5 Free'; } } }

.account-info.changepassword {
	padding-top: 40px; }
