.canhcam-contact-1 {
	padding-bottom: 80px;
	.Module-251 {
		width: 100%;
		.ModuleContent {
			height: 100%;
			width: 100%;
			.map {
				height: 100%;
				width: 100%;
				box-shadow: 0 0 5px rgba(0,0,0,.1);
				iframe {
					height: 100%;
					width: 100%; } } } }
	.form-content {
		.module-title {
			font-size: 25px;
			font-weight: bold;
			color: #414042;
			text-transform: uppercase; }
		.wrap-form {
			>div {
				>p {
					font-size: 15px;
					font-weight: 400;
					color: darken(white,50%);
					margin-bottom: 20px; } }
			.frm-wzd-wrapper {
				display: flex;
				flex-wrap: wrap;
				margin: 0 -10px;
				.form-group {
					margin-bottom: 20px;
					input {
						height: 40px;
						border-radius: 20px;
						border: 1px solid darken(white, 12%);
						width: 100%;
						padding: 0 20px; }
					textarea {
						height: 120px;
						border-radius: 5px;
						border: 1px solid darken(white, 12%);
						width: 100%;
						padding: 0 20px; } } }
			.frm-btnwrap {
				label {
					display: none; }
				.frm-btn-reset {
					display: none; }
				.frm-btn-submit {
					height: 50px;
					width: 200px;
					display: flex;
					justify-content: center;
					align-items: center;
					font-size: 14px;
					font-weight: 600;
					color: white;
					border: 0;
					border-radius: 25px;
					text-transform: uppercase;
					background-image: linear-gradient( 90deg, rgb(46,177,53) 0%, rgb(0,152,74) 100%, rgb(78,147,12) 100%, rgb(82,194,23) 100%, rgb(117,165,80) 100%, rgb(110,175,64) 100%, rgb(122,193,29) 100%, rgb(180,217,133) 100%, rgb(168,212,113) 100%, rgb(162,210,97) 100%, rgb(176,214,102) 100%); } } } } }
