.cello-spList {
	.Module-229 {
		padding-bottom: 30px;
		@media (min-width: 992px) {
			padding-bottom: 50px; } }
	.productpager {
		display: none; }
	.productSlider {
		margin-bottom: 30px;
		@media (min-width: 768px) {
			margin-bottom: 0; }
		@media (min-width: 992px) {
			padding: 0 25px; }
		@media (min-width: 1200px) {
			padding: 0 50px; } }
	.bigImages {
		margin-bottom: 30px;
		.swiper-slide {
			a {
				display: inline!important; }
			img {
				width: 100%; } } }
	.thumbImages {
		max-width: 100%;
		width: 390px;
		.swiper-slide {
			border: 1px solid darken(white,16%);
			opacity: .3;
			transition: ease-out .3s;
			&.swiper-slide-active {
				opacity: 1;
				border-radius: 5px;
				overflow: hidden; } } }
	h1 {
		font-weight: 700;
		color: lighten(black,20%);
		text-transform: uppercase;
		font-size: 1rem;
		margin-bottom: 1rem;
		@media (min-width: 768px) {
			font-size: r(19px); }
		@media (min-width: 992px) {
			font-size: r(22px); }
		@media (min-width: 1200px) {
			font-size: r(25px); } }
	.cobra {
		display: flex;
		align-items: center; }
	.code {
		flex: 0 0 50%;
		max-width: 50%;
		span {
			font-size: r(14px);
			font-weight: 400;
			&:first-child {
				color: darken(white, 40%); }
			&:last-child {
				color: lighten(black, 20%); } } }
	.brand {
		flex: 0 0 auto;
		width: auto;
		max-width: 100%;
		img {
			width: 120px; } }
	.productInfoBlock-1 {
		padding-bottom: 1rem;
		border-bottom: 1px solid darken(white,16%);
		margin-bottom: 10px; }
	.table {
		padding-top: .75rem; }
	.priceBox {
		padding-bottom: .5rem;
		border-bottom: 1px solid darken(white,16%);
		.tr {
			font-size: r(14px);
			font-weight: 400;
			color: darken(white, 40%);
			margin-bottom: .25rem; }
		.price {
			font-size: r(25px);
			font-weight: 500;
			color: #ff6600;
			margin-bottom: .5rem; }
		.pre-order-price {
			p {
				font-size: 16px;
				color: #333333;
				.pre-order, .date-pre-order {
					font-size: 21px;
					color: #2eb135; } } }
		.saving {
			span:nth-child(2) {
				color: #ff6600; } } }
	.productInfo {
		padding-top: .5rem;
		.tr {
			display: flex;
			align-items: center;
			margin-bottom: 1rem;
			span:first-child {
				font-size: r(14px);
				font-weight: 500;
				color: darken(white, 40%);
				flex: 0 0 130px; }
			span:last-child {
				font-size: r(14px);
				font-weight: 400;
				color: lighten(black, 20%); }
			p:last-child {
				font-size: r(14px);
				font-weight: 400;
				display: flex;
				flex-direction: column;
				span {
					flex: none;
					color: lighten(black, 20%); }
				a {
					font-size: 14px;
					text-decoration: underline;
					color: lighten(black, 40%);
					font-style: italic; } }
			&.status {
				span:last-child {
					color: lighten(black,20%);
					font-weight: 400; } }
			&.color {
				.list {
					display: flex;
					flex-wrap: wrap;
					max-width: 100%;
					flex-grow: 1;
					flex-basis: 0;
					a {
						display: inline-block;
						padding: 2px;
						overflow: hidden;
						margin: 0;
						flex: 0 0 20%;
						max-width: 20%;
						img {
							max-width: 100%; } } }
				span:last-child {
					flex-basis: 0;
					flex-grow: 1;
					display: flex;
					a {
						display: flex;
						justify-content: center;
						align-items: center;
						width: 46px;
						height: 46px;
						border: 1px solid transparent;
						border-radius: 3px;
						&.active {
							border: 1px solid #0ba147; } } } }
			&.quantity {
				>span:last-child {
					border-radius: 23px;
					box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1);
					display: flex;
					width: 135px;
					* {
						flex: 0 0 45px;
						max-width: 45px;
						height: 45px;
						display: flex;
						justify-content: center;
						align-items: center; }
					span {
						font-size: r(25px);
						font-weight: 500;
						color: darken(white, 40%);
						cursor: pointer;
						&:hover {
							color: #ff6600; } }
					input {
						font-size: r(18px);
						font-weight: 400;
						color: lighten(black, 20%);
						border: 0;
						text-align: center;
						font-weight: 500; } } } } }
	.productButton {
		max-width: 410px;
		width: 100%;
		margin-bottom: 20px;
		.btnBuy {
			display: inline-flex;
			justify-content: center;
			align-items: center;
			font-size: r(14px);
			font-weight: 600;
			color: white;
			max-width: 48%;
			width: 100%;
			height: 50px;
			text-transform: uppercase;
			border-radius: 25px;
			&:first-child {
				background-image: linear-gradient( 90deg, rgb(46,177,53) 0%, rgb(0,152,74) 100%, rgb(78,147,12) 100%, rgb(82,194,23) 100%, rgb(117,165,80) 100%, rgb(110,175,64) 100%, rgb(122,193,29) 100%, rgb(180,217,133) 100%, rgb(168,212,113) 100%, rgb(162,210,97) 100%, rgb(176,214,102) 100%); }
			&:last-child {
				background-color: rgb(255, 102, 0); } } }
	.socialShare {
		margin-bottom: 20px;
		a {
			margin-right: .5rem; } }
	.giftBox {
		margin-top: 15px;
		margin-bottom: 10px;
		h4 {
			display: flex;
			justify-content: center;
			align-items: center;
			text-transform: uppercase;
			font-size: r(14px);
			font-weight: 600;
			color: white;
			min-height: 40px;
			background: #0ba147;
			border: 1px solid #0ba147;
			border-bottom: 0;
			border-top-left-radius: 4px;
			border-top-right-radius: 4px;
			span:first-child {
				margin-right: .75rem; } }
		.bottom {
			border: 1px solid #0ba147;
			border-top: 0;
			font-size: r(13px);
			font-weight: 400;
			color: darken(white,40%);
			padding: 0 10px .75rem 10px;
			span:first-child {
				color: #ff0000; } }
		.item {
			border-left: 1px solid #0ba147;
			border-right: 1px solid #0ba147;
			padding: 10px;
			display: flex;
			.img {
				flex: 0 0 50px;
				img {
					width: 100%; } }
			.caption {
				padding-left: 1rem;
				p {
					font-size: r(13px);
					font-weight: 500;
					color: lighten(black,20%); }
				.price {
					font-size: r(13px);
					font-weight: 700;
					color: #ff6600; } } } }
	.support {
		padding: .75rem 10px;
		border: 1px solid darken(white,12%);
		border-radius: 4px;
		h5 {
			font-size: r(14px);
			font-weight: 600;
			color: lighten(black,20%);
			margin-bottom: .3rem; }
		.hotline {
			display: flex;
			span {
				font-size: r(16px);
				font-weight: 600;
				color: #ff6600;
				margin-right: 10px; }
			.icon {
				display: flex;
				a {
					width: 25px;
					margin-right: 10px;
					img {
						width: 100%; } } } }
		ul {
			li {
				font-size: r(13px);
				font-weight: 400;
				color: lighten(black,20%);
				display: flex;
				margin-bottom: .5rem;
				&:before {
					margin-right: 10px;
					content: '\f058';
					color: #0ba147;
					font-size: 13px;
					font-weight: 900;
					font-family: 'Font Awesome 5 Free'; } } }

		.block-1 {
			border-bottom: 1px solid darken(white,12%);
			padding-bottom: 12px; }
		.block-2 {
			border-bottom: 1px solid darken(white,12%);
			padding-top: 10px; }
		.block-3 {
			border-bottom: 1px solid darken(white,12%);
			display: flex;
			justify-content: space-between;
			padding: 10px 0;
			a {
				color: #ff6600;
				font-size: r(20px); } }
		.block-4 {
			padding: 10px 0;
			a {
				margin-right: 10px; } } }
	.productDetail {
		padding-bottom: 2rem;
		background: white;
		margin-bottom: 2rem; }
	.blockTitle {
		font-size: r(25px);
		font-weight: 700;
		color: #414042;
		text-transform: uppercase;
		margin-bottom: .75rem;
		display: inline-block;
		@media (max-width: 991.9px) {
			font-size: 21px; }
		@media (max-width: 575.9px) {
			font-size: 18px; } }
	.product-description {
		margin-bottom: 2rem;
		.content {
			padding: 24px 20px;
			background: white;
			box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1);
			max-height: 300px;
			overflow-y: hidden;
			padding-bottom: 120px;
			position: relative;
			&:before {
				content: '';
				position: absolute;
				z-index: 2;
				width: 100%;
				height: 150px;
				bottom: 0;
				left: 0;
				background: rgb(255,255,255);
				background: -moz-linear-gradient(270deg, rgba(255,255,255,0.25) 0%, rgba(255,255,255,0.8) 25%, rgba(255,255,255,1) 50%, rgba(255,255,255,1) 100%);
				background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(255,255,255,0.25)), color-stop(50%, rgba(255,255,255,0.8)), color-stop(100%, rgba(255,255,255,1)));
				background: -webkit-linear-gradient(270deg, rgba(255,255,255,0.25) 0%, rgba(255,255,255,0.8) 25%, rgba(255,255,255,1) 50%, rgba(255,255,255,1) 100%);
				background: -o-linear-gradient(270deg, rgba(255,255,255,0.25) 0%, rgba(255,255,255,0.8) 25%, rgba(255,255,255,1) 50%, rgba(255,255,255,1) 100%);
				background: -ms-linear-gradient(270deg, rgba(255,255,255,0.25) 0%, rgba(255,255,255,0.8) 25%, rgba(255,255,255,1) 50%, rgba(255,255,255,1) 100%);
				background: linear-gradient(180deg, rgba(255,255,255,0.25) 0%, rgba(255,255,255,0.8) 25%, rgba(255,255,255,1) 50%, rgba(255,255,255,1) 100%);
				filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#FFFFFF', endColorstr='#FFFFFF',GradientType=0 ); }
			* {
				margin-bottom: 20px; }
			h3 {
				font-size: r(18px);
				font-weight: 600;
				color: lighten(black,20%); }
			p {
				font-size: r(14px);
				font-weight: 400;
				color: lighten(black,20%);
				span {
					color: #ff6600; } }
			ul,li {
				font-size: .875rem;
				font-weight: 400;
				color: #333; }
			&.active {
				max-height: unset;
				overflow-y: hidden;
				padding-bottom: 0;
				&:before {
					content: none; } }
			.viewmore {
				display: flex;
				max-width: 180px;
				margin: auto;
				justify-content: center;
				align-items: center;
				height: 35px;
				border-radius: 18px;
				text-transform: uppercase;
				color: #999999;
				border: 1px solid #0ba147;
				cursor: pointer;
				position: absolute;
				bottom: 10px;
				right: 0;
				left: 0;
				z-index: 3; }
			img {
				width: 100%; } } }
	.infoDetail {
		margin-bottom: 2rem;
		.infoBox {
			padding: 20px;
			background: white;
			box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1);
			.infoTable {
				border: 1px solid darken(white,8%);
				border-radius: 5px;
				.tr {
					display: flex;
					align-items: center;
					.td:first-child {
						flex: 0 0 150px;
						padding-left: 1rem;
						font-size: r(14px);
						font-weight: 500;
						color: darken(white,40%);
						height: 100%;
						display: flex;
						align-items: center;
						padding: 12px 0 12px 1rem; }
					.td:last-child {
						flex-grow: 1;
						flex-basis: 0;
						font-size: r(14px);
						font-weight: 400;
						color: lighten(black,20%);
						border-left: 1px solid darken(white,8%);
						height: 100%;
						display: flex;
						align-items: center;
						padding: 12px 0 12px 1rem; }
					&:not(:last-child) {
						border-bottom: 1px dashed darken(white,8%); } } } } }
	.productMuaKem {
		position: relative;
		.swiper-container {
			margin: -7px;
			padding: 7px; }
		.swiper-navigation {
			.swiper-button-prev,.swiper-button-next {
				background: none;
				top: 0;
				margin-top: 0;
				height: 37px;
				width: 20px;
				display: flex;
				justify-content: center;
				align-items: center;
				font-size: r(22px);
				color: darken(white,40%);
				&:focus {
					outline: none; } }
			.swiper-button-prev {
				left: unset;
				right: 30px; }
			.swiper-button-next {
				left: unset;
				right: 0; } } }
	.newsRelated {
		.item {
			display: flex;
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 0; }
			.img {
				max-width: 170px;
				flex: 0 0 50%;
				border-radius: 4px;
				overflow: hidden;
				img {
					transition: ease-out .3s; } }
			.caption {
				padding-left: 1rem;
				display: flex;
				flex: 0 0 50%;
				flex-wrap: wrap;
				text-decoration: none;
				h4 {
					font-size: r(15px);
					font-weight: 500;
					color: lighten(black, 20%); } }

			.createdDate {
				font-size: r(13px);
				color: color-rgba(lighten(black,20%),.5);
				span:last-child {
					margin-left: .25rem; } }
			&:hover {
				.img {
					img {
						transform: scale(1.1); } } } } }
	.video {
		margin-bottom: 2rem;
		position: relative;
		.content {
			display: flex;
			flex-wrap: wrap;
			img {
				width: 100%; }
			.item {
				margin-bottom: 10px;
				width: 100%;
				&:not(:nth-child(1)) {
					flex: 0 0 25%;
					max-width: 25%;
					padding: 0 5px; }
				@media (max-width: 767.9px) {
					flex: 0 0 50%;
					max-width: 50%; }
				@media (max-width: 575.9px) {
					flex: 0 0 100%;
					max-width: 100%; }
				.playbutton {
					position: absolute;
					width: 100px;
					height: 100px;
					border-radius: 50%;
					top: 50%;
					left: 50%;
					transform: translate(-50%,-50%); } } } }
	.productSlider-2 {
		padding-bottom: 3rem;
		margin-bottom: 3rem;
		border-bottom: 1px solid darken(white,16%);
		.swiper-container {
			margin: -10px;
			padding: 10px; }
		&:last-child {
			border-bottom: 0;
			margin-bottom: 0; } }
	.productDetail-2 {
		margin-bottom: 3rem;
		.col-lg-4 {
			@media (max-width: 991.98px) {
				order: 1; } }
		.col-lg-8 {
			@media (max-width: 991.98px) {
				order: 2; } } } }

