.cello-khuyenmai {
	background: white;
	@include media-breakpoint-up(lg) {
		.cello-navTab {
			.cello-tabList {
				.item {
					background: #fff;
					@include bor-rad(50px);
					box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1); } } }
		.cello-tabContent {
			.list-item {
				.item {
					&:hover {
						background: #fff; } } } } } }
